			margin-bottom: 30px;
			text-align: center;
			li {
				display: inline;
				padding: 0 10px;
				border-right: 1px solid $white;
				a {
					&:link {
						color: $white;
					}
					&:visited {
						color: $white;
					}
					&:hover {
						color: $color_tan_approx;
					}
				}
				&.lastChild {
					border-right: none;
				}
			}
		}
		&.submitList {
			text-align: center;
			overflow: hidden;
			margin: 30px 0;
			padding: 0 0 0 0;
