				display: inline-block;
				font-size: 16px;
				margin: 0 5px;
				input:hover {
					text-decoration: none;
					color: $white;
				}
			}
		}
		&.creditList {
			overflow: hidden;
			text-align: center;
			margin-bottom: 20px;
			clear: both;
			li {
				display: inline;
				img {
					height: 50px;
					width: auto;
				}
			}
		}
		&.tabs {
			list-style: none;
			width: 100%;
			li {
				float: left;
				border-right: 1px solid $color_celeste_approx;
				border-top: 1px solid $color_celeste_approx;
				background-color: $color_gallery_approx;
				&:first-child {
					border-left: 1px solid $color_celeste_approx;
				}
				&.active {
					border-bottom: 1px solid $white;
					background-color: $white;
					margin-bottom: -1px;
					a {
						font-weight: 600;
						color: $black;
					}
				}
				a {
					display: block;
					padding: 5px 10px;
					color: $color_tapa_approx;
					letter-spacing: -1px;
					outline: none;
					text-decoration: none;
					font-family: $font_4, $font_5, $font_6;
				}
			}
		}
		&.menu li a {
			width: 215px;
			padding: 8px 15px 8px 20px;
			background: $url_4 no-repeat 0 10px;
			display: block;
			border-bottom: 1px dotted $color_kabul_approx;
			margin-top: -1px;
			font-size: 100%;
