	.size10 {
		font-size: 84%;
	}
	.size14 {
		font-size: 117%;
	}
	.size18 {
		font-size: 150%;
	}
	.pd0 {
		padding: 0;
	}
	.registerWrap {
		background: $black;
		opacity: 0.8;
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 50;
	}
	#regist_form {
		margin: 50px auto 0;
		width: 960px;
	}
	.registerP {
		position: absolute;
		top: 400px;
		left: 0;
		right: 0;
		margin: auto;
		z-index: 100;
		color: $white;
		width: 500px;
		text-align: center;
		height: 1.6em;
		font-size: 172%;
	}
	.loginTable {
		width: 960px;
		margin: auto;
		td {
			&.left {
				vertical-align: top;
				width: 320px;
				padding-right: 30px;
				padding-top: 0px;
				border-right: 1px solid $gray;
				text-align: center;
			}
			&.right {
				width: 630px;
				padding-left: 25px;
				div {
					margin-bottom: 15px;
				}
			}
			a:hover {
				text-decoration: none;
				span {
					color: $white;
				}
			}
		}
	}
	.loginBtn {
		background: #282828;
		font-size: 18px;
		padding: 16px 114px;
		color: $color_alto_approx;
		border: none;
		font-weight: bold;
		&:hover {
			color: $white;
		}
	}
	div {
		&.tabs_content {
			width: 718px;
			border: 1px solid $color_celeste_approx;
			> div {
				padding: 20px;
			}
		}
		a span.logonBtn {
			margin-bottom: 30px;
		}
	}
	input::-webkit-input-placeholder {
		color: $color_mountain_mist_approx !important;
	}
	input {
		&:-moz-placeholder {
			color: $color_mountain_mist_approx !important;
		}
		&.formText {
			width: 100%;
		}
	}
	.backBtn {
		background: #fff;
		font-size: 18px;
		padding: 16px 114px;
		font-weight: bold;
		border:2px solid #000;
		color: #000;
		&:hover {
			color: #000!important;
		}
	}
	.submitBtn {
		background: #282828;
		font-size: 18px;
		padding: 16px 114px;
		border:2px solid #282828;
		color: $color_alto_approx;
		font-weight: bold;
		&:hover {
			color: $white;
		}
	}
	.confirm_red {
		font-size: 19px;
		font-weight: bold;
		color: $red;
		text-align: center;
		line-height: 30px;
	}
	.center {
		text-align: center;
	}
	.dammyBtn {
		background: $color_burgundy_approx;
		background: linear-gradient(to bottom, $color_burgundy_approx 4%,$color_venetian_red_approx 100%);
		filter: progid:dximagetransform.microsoft.gradient(startcolorstr='$color_burgundy_approx', endcolorstr='$color_venetian_red_approx',gradienttype=0 );
		font-size: 18px;
		padding: 11px 0;
		border: none;
		text-align: center;
		width: 250px;
		display: block;
		color: $color_alto_approx;
		margin: auto;
	}
	.required {
		color: $red;
	}
	#mail_send_btn {
		margin-bottom: 15px;
	}
	.input_boxstyle {
		width: 95%;
		height: 50px;
		font-size: 20px;
		padding: 5px 20px;
		color: #7e7e7e;
		background: #ededed;
	}
	.addBtn {
		padding: 5px;
		background: $color_burgundy_approx;
		background: linear-gradient(to bottom, $color_burgundy_approx 4%,$color_venetian_red_approx 100%);
		filter: progid:dximagetransform.microsoft.gradient(startcolorstr='$color_burgundy_approx', endcolorstr='$color_venetian_red_approx',gradienttype=0 );
		color: $color_alto_approx;
		width: 180px;
		text-align: center;
	}
	.p120 {
		padding: 10px 120px;
	}
	#slideshow {
		margin: 0 auto 2em;
		width: 660px;
		text-align: left;
		div {
			width: 660px;
			height: 495px;
			overflow: hidden;
			position: relative;
			div {
				top: 0;
				left: 0;
				position: absolute;
			}
		}
		ul {
			width: 660px;
			li {
				width: 132px;
				float: left;
				display: inline;
			}
		}
		.active {
			filter: alpha(opacity=100)!important;
			opacity: 1!important;
		}
	}
	.clear {
		clear: both;
	}
	.image_carousel {
		padding: 305px 0 35px 390px;
		img {
			border: 1px solid $color_celeste_approx;
			background-color: $white;
			padding: 9px;
			margin: 7px;
			display: block;
			float: left;
		}
	}
	.wpcf7-textarea {
		width: 90%;
	}
	.wpcf7-text {
		width: 60%;
	}
	.zip1 {
		width: 60px;
	}
	.zip2 {
		width: 100px;
	}
	.formtel {
		width: 100px;
		height: 16px;
	}
	.addr {
		width: 90%;
	}
	.contactformsubmit {
		text-align: center;
	}
	.widget_search {
		background: $white;
		margin-bottom: 20px;
		padding: 15px;
		text-align: center;
	}
	.ie8 #jMenu li ul {
		background: $color_persian_plum_approx;
	}
	.opacity:hover {
		filter: alpha(opacity=60);
		opacity: 0.6;
	}
	a span {
		&.logonBtn {
			display: block;
		}
		&.loginBtn2 {
			display: block;
		}
	}
	.active a {
		background: $color_shark_approx;
		background: linear-gradient(to bottom, $color_shark_approx 0,$color_rangoon_green_approx 100%);
		filter: progid:dximagetransform.microsoft.gradient(startcolorstr='$color_shark_approx', endcolorstr='$color_rangoon_green_approx',gradienttype=0 );
		color: $color_alto_approx !important;
		padding: 5px 10px;
		width: 230px;
		display: block;
		text-align: center;
		&:hover {
			color: $white !important;
			text-decoration: none;
		}
	}
	.submitBtn2 a {
		background: #282828;
		font-size: 18px;
		line-height: 50px;
		width: 250px;
		border:2px solid #282828;
		color: $color_alto_approx;
		font-weight: bold;
		display: block;
		text-align: center;
		&:link {
			color: $color_alto_approx;
		}
		&:visited {
			color: $color_alto_approx;
		}
		&:hover {
			text-decoration: none;
			color: $white;
		}
	}
	p.tex14 {
		margin-bottom: 20px;
	}
	#regist_pankuzu ul li {
		display: inline;
		padding: 10px;
		&.active {
			background: $color_shark_approx;
			background: linear-gradient(to bottom, $color_shark_approx 0,$color_rangoon_green_approx 100%);
			filter: progid:dximagetransform.microsoft.gradient(startcolorstr='$color_shark_approx', endcolorstr='$color_rangoon_green_approx',gradienttype=0 );
			color: $white;
		}
		&.passive {
			background: $color_celeste_approx;
			color: $white;
		}
	}
	.wpcf7-form-control wpcf7-text zip {
		width: 30%;
	}
	.sideSectionw ul {
		margin-bottom: 20px;
		li {
			padding-bottom: 8px;
			padding-left: 15px;
			margin-bottom: 8px;
			background: $url_6 no-repeat 0 5px;
			border-bottom: 1px dotted $color_kabul_approx;
		}
	}
}
